import React from "react";
import { FaUser, FaShoppingCart, FaSearch, FaBars } from "react-icons/fa";
import ResponsiveNavigationMenu, { 
  NavigationMenuList, 
  NavigationMenuItem, 
  NavigationMenuLink, 
  NavigationMenuTrigger, 
  NavigationMenuContent 
} from "@/components/ui/navigation-menu";

export default function Header() {
    return (
        <header className="fixed top-0 left-0 w-full z-50 bg-white shadow">
            <div className="w-full bg-blue-600 text-white text-center text-sm py-1">
                Ganhe <span className="bg-blue-950 rounded ">R$10,00</span> de desconto no seu primeiro pedido. Utilize o cupom <span className="font-semibold">DESCONTO10</span>.
            </div>

            {/* Top bar with logo and search */}
            <div className="bg-white p-4">
                <div className="container mx-auto flex items-center justify-between">
                    <div className="flex items-center space-x-8">
                        <img src="/avanti-logo.png" alt="Avanti" className="h- 8" style={{ filter: "invert(1)" }} />
                    </div>
                    <div className="flex-1 mx-8 relative border border-gray-200 rounded-lg">
                        <input
                            type="text"
                            placeholder="Digite aqui o que você procura"
                            className="w-full p-2 rounded-lg text-gray-800 outline-none"
                        />
                        <FaSearch className="absolute right-3 top-1/2 transform -translate-y-1/2 text-gray-400" />
                    </div>
                    <div className="flex items-center space-x-6">
                        <div className="flex items-center space-x-2">
                            <FaUser />
                            <span>Olá, <br></br>Nome cliente</span>
                        </div>
                        <div className="flex items-center space-x-2">
                            <FaShoppingCart />
                            <span>0</span>
                        </div>
                    </div>
                </div>
            </div>

            {/* Navigation bar */}
            <div className="bg-white shadow-md">
                <div className="container mx-auto">
                    <ResponsiveNavigationMenu>
                        <NavigationMenuList className="flex space-x-6 p-4">
                            <NavigationMenuItem>
                                <NavigationMenuTrigger className="flex items-center gap-2 bg-white text-black font-medium">
                                    <FaBars className="text-lg" />
                                    Todas as Categorias
                                </NavigationMenuTrigger>
                                <NavigationMenuContent>
                                    <div className="w-[900px] p-4 bg-white">
                                        <div className="flex">
                                            {/* Left column - Departments */}
                                            <div className="w-1/4 border-r pr-4">
                                                <ul className="space-y-2">
                                                    {[...Array(10)].map((_, i) => (
                                                        <li key={i}>
                                                            <NavigationMenuLink
                                                                href="#"
                                                                className={`block text-sm px-2 py-1 rounded hover:text-blue-600 hover:bg-gray-100 transition ${i === 0 ? "text-blue-600 font-semibold bg-gray-100" : ""
                                                                    }`}
                                                            >
                                                                Departamento
                                                            </NavigationMenuLink>
                                                        </li>
                                                    ))}
                                                </ul>
                                            </div>
                                            {/* Right columns - Categories */}
                                            <div className="flex-1 pl-4">
                                                <div className="grid grid-cols-3 gap-8">
                                                    {[1, 2, 3].map((col) => (
                                                        <div key={col}>
                                                            <h4 className="font-semibold mb-2 text-blue-600">Categoria</h4>
                                                            <ul className="space-y-1">
                                                                {[...Array(8)].map((_, idx) => (
                                                                    <li key={idx}>
                                                                        <NavigationMenuLink
                                                                            href="#"
                                                                            className="block text-sm text-gray-600 hover:text-blue-600"
                                                                        >
                                                                            Categoria
                                                                        </NavigationMenuLink>
                                                                    </li>
                                                                ))}
                                                            </ul>
                                                        </div>
                                                    ))}
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </NavigationMenuContent>
                            </NavigationMenuItem>

                            {/* Demais departamentos estáticos */}
                            <NavigationMenuItem>
                                <NavigationMenuTrigger className="text-black font-medium hover:text-blue-600 hover:bg-gray-100 transition">
                                    Departamento
                                </NavigationMenuTrigger>
                                <NavigationMenuContent>
                                    <div className="w-[700px] p-4 bg-white">
                                        <div className="grid grid-cols-3 gap-8">
                                            {[1, 2, 3].map((col) => (
                                                <div key={col}>
                                                    <h4 className="font-semibold mb-2 text-blue-600">Categoria</h4>
                                                    <ul className="space-y-1">
                                                        {[...Array(8)].map((_, idx) => (
                                                            <li key={idx}>
                                                                <NavigationMenuLink
                                                                    href="#"
                                                                    className="block text-sm text-gray-600 hover:text-blue-600"
                                                                >
                                                                    Categoria
                                                                </NavigationMenuLink>
                                                            </li>
                                                        ))}
                                                    </ul>
                                                </div>
                                            ))}
                                        </div>
                                    </div>
                                </NavigationMenuContent>
                            </NavigationMenuItem>
                            {/* Repita NavigationMenuItem acima para quantos departamentos quiser */}
                            <NavigationMenuItem>
                                <NavigationMenuLink
                                    href="#"
                                    className="text-black font-medium hover:text-blue-600 hover:bg-gray-100 transition"
                                >
                                    Departamento
                                </NavigationMenuLink>
                            </NavigationMenuItem>
                            <NavigationMenuItem>
                                <NavigationMenuLink
                                    href="#"
                                    className="text-black font-medium hover:text-blue-600 hover:bg-gray-100 transition"
                                >
                                    Departamento
                                </NavigationMenuLink>
                            </NavigationMenuItem>
                            <NavigationMenuItem>
                                <NavigationMenuLink
                                    href="#"
                                    className="text-black font-medium hover:text-blue-600 hover:bg-gray-100 transition"
                                >
                                    Departamento
                                </NavigationMenuLink>
                            </NavigationMenuItem>
                            <NavigationMenuItem>
                                <NavigationMenuLink
                                    href="#"
                                    className="text-black font-medium hover:text-blue-600 hover:bg-gray-100 transition"
                                >
                                    Departamento
                                </NavigationMenuLink>
                            </NavigationMenuItem>
                        </NavigationMenuList>
                    </ResponsiveNavigationMenu>
                </div>
            </div>
        </header>
    );
}